	        name: 'Manu Gopinathan',
	        bio: 'Manu er nyutdannet sivilingeniør med mastergrad i datateknologi fra NTNU. Her spesialiserte han seg i kunstig intelligens. Manu er en engasjert, positiv og ansvarsfull person med gode skriftlige og muntlige kommunikasjonsevner. Han har god kompetanse innen back-end utvikling i Python og Java, samt erfaring med Javascript og CSS. I tillegg har han opparbeidet seg kompetanse innen maskinlæring med vekt på bruken av nevrale nettverk. Fra november 2016 har han vært deltidsansatt i avdelingen til Knowit i Trondheim, hvor han har jobbet med prototyping av en webløsning på Vegloggen-prosjektet for Statens Vegvesen. Fra august 2017, jobber han som fast ansatt hos Knowit i Oslo.',
	        email: 'manu.gopinathan@knowit.no',
